<template>
  <div class="page-badge">

    <mt-header title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <nav class="tab-city">
      <mt-button @click.native="popupVisible1 = true" size="large" ref="button"></mt-button>
      <mt-button @click.native="popupVisible2 = true" size="large" ref="button"></mt-button>
      <mt-popup v-model="popupVisible1" popup-transition="popup-fade" class="city-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li></li>
          <li></li>
          <li class="active"></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </mt-popup>
      <mt-popup v-model="popupVisible2" popup-transition="popup-fade" class="city-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li></li>
          <li></li>
          <li class="active"></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </mt-popup>
    </nav>


    <div class="list-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <ul class="list-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
        <shop-item 
            v-for="(item, index) in list"
            key="index"
            href="post?id=123"
            :image="item"
            title=""
            salary="3500-5000" 
            source=" McDonald's"
            location=""
            welfare=",," />
      </ul>
      <p v-show="loading" class="list-infinite-loading">
        <mt-spinner type="fading-circle"></mt-spinner>
        ...
      </p>
    </div>
    
</div>
</template>

<script>
import commonTitle from '../common/title.vue'
import shopItem from '../common/shopItem.vue'

export default {
  name: 'page-list',

  components: {
      commonTitle,
      shopItem
  },

  data() {
      return {
        popupVisible1: false,
        popupVisible2: false,
        buttonBottom: 0,
        list: [],
        loading: false,
        allLoaded: false,
        wrapperHeight: 0
      };
    },

    methods: {
    loadMore() {
      this.loading = true;
      setTimeout(() => {

        let last = this.list[this.list.length - 1];
        for (let i = 1; i <= 10; i++) {
          this.list.push("http://www.cssoul.com/demo/weihai/company/12.jpg");
        }
        this.loading = false;
      }, 1000);
    }
  },

    created() {
      for (let i = 1; i <= 10; i++) {
        this.list.push("http://www.cssoul.com/demo/weihai/company/"+i+".jpg");
      }
    },

    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;

      this.buttonBottom = this.$refs.button.$el.getBoundingClientRect().bottom+1;
    }
};
</script>

<style>
  .page-list{
    background:#f0f0f0;
  }

  .tab-city{
    display:flex;
  }
  .tab-city .mint-button {
    flex:1;
    background:#fff;
    border-radius:0;
  }
  .tab-city .v-modal{
    top: 86px;
  }

  .city-popup{
    width: 100%;
    height: 250px;
    padding: 0;
    transform: translate(-50%, 0);
    overflow:scroll;
  }
  .mint-popup ul{
    margin:10px 0;
    padding:0 20px;
  } 
  .mint-popup ul li{
    height:40px;
    line-height:40px;
    color:#828282;
  }
  .mint-popup ul .active{
    color: #0977C5;
  }


  .list-infinite-wrapper {
    margin-top: 0;
    overflow: scroll;
  }
  
  .list-infinite-loading {
    text-align: center;
    height: 50px;
    line-height: 50px;
  }
  .list-infinite-loading div {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
  }
  
</style>



