<template>
  <div>
    <router-view></router-view>
  </div>
</template>


<script type="text/babel">
  export default {
    computed: {
      visible() {
        return ['/', '/header', '/search'].indexOf(this.$route.path) < 0;
      }
    }
  };
</script>



